/* Global */

p {
  margin: 0;
  padding: 0;
}


.Character-Board {
  background: #e9e9e9;
  max-width: 665px;
  margin: 20px auto 40px;
  padding: 20px;
}

.Character-Board-Wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.Character-Board-Wrap .character-card {
/* 112 x 163 */

}

.Character-Board-Wrap .character-card {  
  /* overflow: hidden; */
  border-radius: 3px;
  max-width: 112px;
  width: 100%;
  margin: 0 auto 20px auto;
  background: #ffffff;  
  cursor: pointer;
}

.Character-Board-Wrap .character-card:hover {
  transform: rotate(2deg) scale(1.2);
  box-shadow: 2px 3px 0px 1px #cecece, -2px 3px 4px 4px #eaeaea;
}

.Character-Board-Wrap .character-card:hover img {
  transform: rotate(-3deg) scale(1.04);
}

.Character-Board-Wrap .character-card img{
  width: 100%;
  height: auto;
}


.Character-Board-Wrap .character-card .text{
  padding: 0 5px;
  margin-bottom: 5px;
}

.Character-Board-Wrap .character-card .text p {
  font-size: 12px;
}


@media screen and (max-width: 1024px){
  .Character-Board-Wrap .character-card {
    max-width: 240px;
    box-shadow: 2px 3px 0px 1px #cecece, -2px 3px 4px 4px #eaeaea;
  }
}
